import React, { useContext, useEffect, useState } from 'react';
import './home.scss'
import Lorby from '../images/lorby-human.svg'
import { AuthContext } from '../../context/AuthContextProvider';
import Modal from 'react-modal';
Modal.setAppElement('#root');

const Home = () => {
    const {checkAuth, handleLogout, isRegistered, isLogin} = useContext(AuthContext)
    const [logoutModalIsOpen, setLogoutModalIsOpen] = useState(false)

    const openLogoutModal = () => {
        setLogoutModalIsOpen(true)
    }

    const closeLogoutModal = () => {
        setLogoutModalIsOpen(false)
    }

    useEffect(() => {
        if  (localStorage.getItem('tokens')) {
            checkAuth()
        }
    })

    return (
        <div className='home-container'>
            {
            isRegistered            
            ?             
            <h1> !</h1> 
            : 
            isLogin
            ? 
            <h1> !</h1> 
            : 
            null
            }

            <Modal
            isOpen={logoutModalIsOpen}
            onRequestClose={closeLogoutModal}
            contentLabel='    '
            className='logout-modal'
            >
                <h3>?</h3>
                <h4> ?</h4>
                <button onClick={handleLogout}>, </button>
                <p onClick={closeLogoutModal}>, </p>
            </Modal>
                
                <p className='lorby-descr'>Lorby -   </p>
                <img src={Lorby} alt="" />
                <p onClick={openLogoutModal} className='logout'></p>
        </div>
    );
};

export default Home;